.product{
    display: flex;
    flex-direction: column;
    padding: 20px;
    margin: 20px 0;
}
.product img{
    padding: 30px;
}
.product-name{
    margin: 0;
}
.product-seller, .product-category{
    text-transform: capitalize;
    font-size: 18px;
    margin: 5px;
    margin-left: 30px;
    display: inline-block;
}
.product-info{
    display: flex;
    align-items:baseline;
    flex-wrap: wrap;
    margin: 20px 0;
}
.product-price{
    font-size: 35px;
    font-weight: bold;
    margin: 0 20px 10px 0;
}
.product-stock{
    font-size: 22px;
    margin: 10px 0 0; 
}
.product-rating{
    margin: 0 0 10px 20px;
    font-size: 20px;
}
.product-star-count{
    font-size: 20px;
    display: inline;
    margin: 0 0 0 10px;
}
.cart-icon{
    margin-right: 10px;
}

.icon-color{
    color: var(--theme-color);
}

@media screen and (min-width: 768px) {
    .product{
        flex-direction: row;
    }
}